import {EventEmitter, Injectable} from "@angular/core";
import {BotGraph, ToggleDetailEvent} from "../models/response.model";

@Injectable()
export class LayoutService {
  toggleDetailPanelEvent: EventEmitter<ToggleDetailEvent> = new EventEmitter();
  onSetDetailData: EventEmitter<BotGraph> = new EventEmitter();

  toggleDetailPanel(isShow: boolean, isRightSide: boolean) {
    let event: ToggleDetailEvent = {
      'isShow': isShow,
      'isRightSide': isRightSide
    };
    this.toggleDetailPanelEvent.emit(event);
  }

  setDetailData(graph: BotGraph) {
    this.onSetDetailData.emit(graph);
  }
}



