import {EventEmitter, Injectable, Output} from "@angular/core";
import * as global from '../common/global';
import {HttpClient, HttpHeaders, HttpParams} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {Router} from "@angular/router";
import {RES} from "../models/response.model";

@Injectable()
export class AuthService {
  @Output() usernameEvent: EventEmitter<string> = new EventEmitter();

  constructor(private http: HttpClient, private router: Router){
  }

  login(username: string, password: string):  Observable<string> {
    const body = new HttpParams()
      .set('username', username)
      .set('password', password);

    return this.http.post(global.BASE_API_URL + '/convbot/users/login',
      body.toString(),
      {
        headers: new HttpHeaders({
          'Content-Type': 'application/x-www-form-urlencoded',
          'Authorization': "Basic " + btoa(username + ':' + password)
        }),
        observe: 'response'
      }).map(res => {
      let token = res.headers.get('Authorization');
      if (token) {
        this.saveSession(username,token);
      }
      return token;
    })
  }

  isAuthenticated() {
    return sessionStorage.getItem('otyToken') !== null;
  }

  getUsername(){
    return sessionStorage.getItem('otyUsername');
  }

  logout():  Observable<boolean> {
    return this.http.get(global.BASE_API_URL + '/convbot/users/logout').map((res: RES) => {
      this.deleteSession();
      return res.success;
    });
  }

  saveSession(username:string,token:string){
    sessionStorage.setItem('otyUsername', username);
    sessionStorage.setItem('otyToken', token);
    this.usernameEvent.emit(username);
  }

  deleteSession(){
    sessionStorage.removeItem('otyUsername');
    sessionStorage.removeItem('otyToken');
    this.usernameEvent.emit(null);
  }

}



