import {EventEmitter, Injectable, Output} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from "rxjs/Observable";

import {
  ConversationHistory,
  ConversationReq,
  ConversationRes,
  DialogHistory,
  RES,
  UserRequest, UnfinishedTopic
} from '../models/response.model';
import * as global from '../common/global';

@Injectable()
export class ConversationService {
  private dialogHistory: DialogHistory[];
  private conversationHistoryList: ConversationHistory[];
  private latestConversationData: ConversationRes;
  @Output() onHistoryConversationChange: EventEmitter<any> = new EventEmitter();
  @Output() onRerunCurrentConversation: EventEmitter<UnfinishedTopic> = new EventEmitter();
  @Output() onFormatConversation: EventEmitter<ConversationRes> = new EventEmitter();
  @Output() scrollConversationHistoryList: EventEmitter<any> = new EventEmitter();

  constructor(private http: HttpClient) {
    this.conversationHistoryList = [];
  }

  startConversation(projectId: string): Observable<ConversationRes> {
    return this.http.get(global.BASE_API_URL + '/convbot/conversation/' + projectId).map((res: RES) => res.data);
  }

  historyConversationChange(){
    this.onHistoryConversationChange.emit();
  }
  setRerunCurrentConversation(unfinishedTopic: UnfinishedTopic){
    this.onRerunCurrentConversation.emit(unfinishedTopic);
  }

  setDialogHistory(dialogHistory: DialogHistory[]) {
    this.dialogHistory = dialogHistory;
  }
  getDialogHistory(): DialogHistory[] {
    return this.dialogHistory;
  }
  continueConversation(projectId: string, userRequest: UserRequest, userControlType?: string): Observable<ConversationRes> {
    let dialogId = this.getDialogHistory().slice(-1)[0].dialogId;
    userControlType = userControlType || "conversation";

    let conversation: ConversationReq = {
      dialogHistory: this.getDialogHistory(),
      userControlType: userControlType,
      userRequest: userRequest
    };
    return this.http.post(global.BASE_API_URL + '/convbot/conversation/' + projectId + "/" + dialogId, conversation)
      .map((res: RES) => res.data);
  }

  getGraphData(projectId: string): Observable<any> {
    return this.http.get(global.BASE_API_URL + '/convbot/graph/' + projectId)
      .map((res: RES) => res.data);
  }
  saveGraphData(projectId: string, graphData: any): Observable<any> {
    return this.http.post(global.BASE_API_URL + '/convbot/graph/' + projectId, graphData)
      .map((res: RES) => res.data);
  }

  initConversationHistoryList(conversations: ConversationHistory[]) {
    this.conversationHistoryList = conversations;
  }

  addConversationHistoryList(res: ConversationRes) {
    let lastDialogHistory = res.dialogHistory.slice(-1)[0];
    this.latestConversationData = res;
    this.conversationHistoryList.push({
      dialogState: lastDialogHistory,
      content: {
        botGreetingCaption: res.botGreetingCaption,
        botResponseCaption: res.botResponseCaption
      }
    });
    this.scrollConversationHistoryList.emit();
  }

  getConversationHistoryList() {
    return this.conversationHistoryList;
  }
  clearConversationHistoryList() {
    this.conversationHistoryList = [];
  }
  getUnfinishedTopics(){
    let arr: UnfinishedTopic[];
    if (this.latestConversationData && this.latestConversationData.botResponseData.value.unfinishedTopics) {
      arr = this.latestConversationData.botResponseData.value.unfinishedTopics;
    }
    return arr;
  }

  fireEventFormatConversation(res: ConversationRes) {
    this.onFormatConversation.emit(res);
  }

  /**
   * rerun a conversation from restored conversation list
   * @param {string} projectId
   * @param {string} topicId
   * @returns {Observable<ConversationRes>}
   */
  resumeTopic(projectId: string, topicId: string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "resumeTopic",
        "component" : {
          "topicId" : topicId
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }

  getEntityDetail(projectId: string, entityName: string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "graphAction",
        "component" : {
          "type" : "entity",
          "entityName" : entityName
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }

  deleteEntity(projectId: string, entityName: string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "deleteAction",
        "component" : {
          "type" : "entity",
          "entityName" : entityName
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }
  //**
  getAttributeDetail(projectId: string, entityName: string, attributeId:string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "graphAction",
        "component" : {
          "type" : "attribute",
          "entityName" : entityName,
          "attributeId": attributeId    
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }
  /*
   getAttributeDetail(projectId: string, entityName: string, attributeName:string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "graphAction",
        "component" : {
          "type" : "attribute",
          "entityName" : entityName,
          "attributeName": attributeName    
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  } 
   */ 
  deleteAttribute(projectId: string, entityName: string, attributeName:string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "deleteAction",
        "component" : {
          "type" : "attribute",
          "entityName" : entityName,
          "attributeName": attributeName
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }

  getRelationDetail(projectId: string, relationName: string, sourceEntityName:string, targetEntityName:string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "graphAction",
        "component" : {
          "type" : "relation",
          "relationName": relationName,
          "sourceEntityName": sourceEntityName,
          "targetEntityName": targetEntityName
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }

  deleteRelation(projectId: string, relationName: string, sourceEntityName:string, targetEntityName:string): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "deleteAction",
        "component" : {
          "type" : "relation",
          "relationName": relationName,
          "sourceEntityName": sourceEntityName,
          "targetEntityName": targetEntityName
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }

  deleteDefinition(projectId: string, entityName: string, index: number): Observable<ConversationRes> {
    let userRequest: UserRequest = {
      "key" : "command",
      "type": "complex",
      "value": {
        "name": "deleteAction",
        "component" : {
          "type" : "definition",
          "entityName": entityName,
          "definition": index,
        }
      }
    };
    return this.continueConversation(projectId, userRequest, "uiEvent");
  }
}



