import { NgModule }         from '@angular/core';
import { BrowserModule }    from '@angular/platform-browser';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';

import { AppComponent }     from './ontology.root/app.component';
import { SharedModule }     from './common/shared.module';
import { ProjectsModule }   from './ontology.projects/projects.module';
import { ProjectModule }    from './ontology.project/project.module';
import { NavbarModule }     from './ontology.navbar/navbar.module';
import { FooterModule }     from './ontology.footer/footer.module';

import { CytoscapeService } from './services/cytoscape.service';
import { ProjectService }   from './services/project.service';
import { ConversationService }   from './services/conversation.service';
import {LayoutService} from "./ontology.layout/layout.service";

import { AppRoutingModule } from './app-routing.module';
import {ProjectNewModule} from "./ontology.project.new/project.new.module";
import {NotificationModule} from "./ontology.notification/notification.module";
import {NotificationService} from "./services/notification.service";
import {HttpHandlerInterceptor} from "./http.handler.interceptor";
import {UnfinishedTopicModule} from "./ontology.unfinished.topic/unfinished.topic.module";
import {LoginModule} from "./ontology.login/login.module";
import {AuthService} from "./services/auth.service";
import {AuthGuardService} from "./services/auth-guard.service";
import {HomeLayoutComponent} from "./layouts/home-layout/home-layout.component";
import {LoginLayoutComponent} from "./layouts/login-layout/login-layout.component";

@NgModule({
  declarations: [
    AppComponent,
    HomeLayoutComponent,
    LoginLayoutComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    SharedModule,
    NavbarModule,
    FooterModule,
    ProjectNewModule,
    NotificationModule,
    UnfinishedTopicModule,
    BrowserModule,
    AppRoutingModule    //should be the latest module
  ],
  providers: [
    CytoscapeService,
    ProjectService,
    NotificationService,
    ConversationService,
    AuthService,
    AuthGuardService,
    LayoutService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpHandlerInterceptor,
      multi:true
    }
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule {
}



